import React, { Component } from "react";
import moment from "moment";
import _ from "lodash";
import { Link } from "react-router-dom";
import {
  Button,
  Card,
  CardHeader,
  CardBody,
  CardFooter,
  Input,
  Row,
  Col,
  FormGroup,
  Label,
  Container,
} from "reactstrap";
import ReactDatetime from "react-datetime";
import ErrorSummary from "components/ErrorSummary";
import {
  getDate,
  getDateFormat,
  getDateTime,
  getYearOld,
  getUserFullName,
} from "helpers/UtilsHelper";
import { FormInput } from "components/Form";
import { toast } from "react-toastify";
import ConfirmDeleteModal from "../../../../components/Modal/ConfirmDeleteModal.jsx";
import clientApi from "../../../../api/clientApi";
import cons from "../../../../constants";
import "./ClientEditScreen.scss";

const axios = require("axios");

const documents = cons.SIGN_DOCUMENT.map((item, index) => {
  return {
    name: item,
    isUsable: false,
    id: index,
  };
});

const defaultState = () => {
  return {
    isFormReady: {},
    clientData: [],
    us_state: [],
    errors: {},
    isDeleteAccount: false,
    isGuardianFail: false,
    isEditAccount: true,
    isMinor: false,
    dateOfBirth: null,
    lastName: null,
    address: null,
    relationshipStatus: cons.RELATIONSHIP.SINGLE,
    employmentStatus: 3,
    gender: 0,
    race: 0,
    status: 0,
    documents: documents,
    preferredLanguage: "en",
    signedDocument: "",
    preferredName: "",
    phoneNumber: "",
    middleName: "",
    firstName: "",
    guardian: "",
    suffix: "",
    email: "",
    note: "",
    state: "",
    startDate: "",
    keyPress: "",
  };
};

export default class ClientEditScreen extends Component {
  constructor(props) {
    super(props);
    this.state = defaultState();
  }

  componentDidMount() {
    this.getListClient();
    const clientId = this.props.match.params.id;
    if (!clientId) {
      this.setState({ isEditAccount: false });
    } else {
      clientApi
        .detail(clientId)
        .then((res) => {
          this.convertSignDocument(res.signedDocument);

          this.setDefaultState({
            ...res,
            phoneNumber: this.getFormatPhone(res.phoneNumber),
          });
        })
        .catch((err) => {
          console.error(err);
        });
    }
    this.getState();
  }

  getFormatPhone = (text) => {
    if (!text || text === "null" || text === "undefined") {
      return "";
    } else {
      let newText =
        text.substring(0, 3) +
        "-" +
        text.substring(3, 6) +
        "-" +
        text.substring(6, text.length);
      return newText;
    }
  };

  getState = () => {
    axios
      .get("/location/states.json")
      .then((response) => {
        this.setState({ us_state: response.data });
      })
      .catch(function (error) {
        console.log(error);
      });
  };

  handleChangeState = (e, field) => {
    delete this.state.errors[field];
    let { value } = e.target;
    this.setState({ [field]: value });
  };

  handleChangeLocation = (e, field) => {
    let { value } = e.target;
    this.setState({ [field]: value });
  };

  setDefaultState = (res) => {
    const {
      address,
      dateOfBirth,
      email,
      employmentStatus,
      firstName,
      gender,
      guardian,
      id,
      lastName,
      middleName,
      note,
      phoneNumber,
      preferredLanguage,
      preferredName,
      race,
      relationshipStatus,
      signedDocument,
      status,
      suffix,
      state,
      city,
      zipcode,
      firstSeenAt,
    } = res;
    const date = new Date(dateOfBirth);
    const yearOld = getYearOld(date);

    if (yearOld < 18) {
      this.setState({ isMinor: true });
    } else {
      this.setState({ isMinor: false });
    }
    this.setState({
      address,
      dateOfBirth,
      email,
      employmentStatus,
      firstName,
      gender,
      guardian,
      id,
      lastName,
      middleName,
      note,
      phoneNumber,
      preferredLanguage,
      preferredName,
      race,
      relationshipStatus,
      signedDocument,
      status,
      suffix,
      state,
      city,
      zipcode,
      firstSeenAt,
    });
  };

  convertSignDocument = (signedDocument) => {
    if (signedDocument) {
      const tempArr = signedDocument.split(",");
      const { documents } = this.state;
      documents.forEach((item) => {
        if (tempArr.includes(String(item.id))) {
          item.isUsable = true;
        }
      });
    }
  };

  componentDidUpdate() {
    document.addEventListener("keydown", (e) => {
      if (this.state.keyPress !== e.key) {
        this.setState({ keyPress: e.key });
      }
    });
  }

  handleMultipleSelectDocument = (e) => {
    let tempData = this.state.documents;
    const { name, checked } = e.target;
    tempData[Number(name)].isUsable = checked;
    this.setState({
      documents: tempData,
    });
  };

  convertMultipleSelect = () => {
    const { documents } = this.state;
    const tempArr = documents.filter((item) => item.isUsable);
    let signedDocument = [...tempArr.map((item) => item.id)].join(",");
    this.setState({
      signedDocument: signedDocument,
    });
  };

  handleError = (field, status) => {
    let check = true;
    if (status) {
      for (let key in this.state.isFormReady) {
        check = check && this.state.isFormReady[key];
      }
    }
    this.setState({
      isFormReady: {
        ...this.state.isFormReady,
        [field]: status,
        check: check && status,
      },
    });
  };

  handleCheckBox = (e) => {
    const { name, checked } = e.target;
    if (name === "status") {
      if (checked === true) {
        this.setState({
          status: 0,
        });
      } else {
        this.setState({
          status: 1,
        });
      }
    } else {
      this.setState({
        [name]: checked,
      });
    }
  };

  getListClient = () => {
    clientApi
      .list()
      .then((res) => {
        this.setState({
          clientData: res.result,
        });
      })
      .catch((err) => {
        console.error(err);
      });
  };

  validDate = (current) => {
    var today = moment().subtract(0, "day");
    return current.isBefore(today);
  };

  setDates = (value) => {
    delete this.state.errors["dateOfBirth"];
    delete this.state.errors["guardian"];
    const date = new Date(value);
    const yearOld = getYearOld(date);
    this.setState({ dateOfBirth: value, guardian: "" });
    if (18 > yearOld) {
      this.setState({ isMinor: true }, () => {
        this.checkMinor();
      });
    } else {
      this.setState({ isMinor: false }, () => {
        this.checkMinor();
      });
    }
  };

  setStartDate = (value) => {
    this.setState({ startDate: value });
  };

  checkMinor = () => {
    const { isMinor, guardian } = this.state;
    if (isMinor && guardian === "") {
      this.setState({ isGuardianFail: true });
    } else {
      this.setState({ isGuardianFail: false });
    }
  };

  handleChange = async (e) => {
    const { name, value } = e.target;
    delete this.state.errors[name];
    delete this.state.errors["nonFieldErrors"];

    if (
      (name === "phoneNumber" && this.state.phoneNumber.length !== 12) ||
      this.state.keyPress === "Backspace"
    ) {
      await this.setState({ [name]: value });
      if (this.state.keyPress !== "Backspace") {
        if (this.state.phoneNumber.length === 3) {
          await this.setState({ phoneNumber: this.state.phoneNumber + "-" });
        }
        if (this.state.phoneNumber.length === 7) {
          await this.setState({ phoneNumber: this.state.phoneNumber + "-" });
        }
      }
    }

    if (name !== "phoneNumber") {
      this.setState({ [name]: value });
    }
    if (name === "guardian" && value) {
      this.setState({
        isGuardianFail: false,
      });
    } else if (name === "guardian" && !value) {
      this.setState({
        isGuardianFail: true,
      });
    }
  };

  handleSelectionChange = (e, field) => {
    let { value } = e.target;
    if (field === "preferredLanguage") {
      this.setState({
        [field]: value,
      });
    } else {
      this.setState({
        [field]: Number(value),
      });
    }
  };

  focusFirstError = () => {
    const element = document.querySelectorAll(
      ".form__controller--error input"
    )[0];
    if (element) {
      element.focus();
    }
  };

  handleFile = (event) => {
    const { name, files } = event.target;
    if (files.length) {
      this.setState({ [name]: files[0] });
    }
  };

  saveForm = async (e) => {
    await this.convertMultipleSelect();
    const { isEditAccount, dateOfBirth } = this.state;
    if (!_.isNull(dateOfBirth) && !_.isString(dateOfBirth)) {
      this.setState({ dateOfBirth: getDate(dateOfBirth) });
    }
    if (isEditAccount) {
      this.updateClient();
    } else {
      this.registerClient();
    }
  };

  registerClient = async () => {
    let newPhoneNumber = this.state.phoneNumber.replaceAll("-", "");
    const data = this.state;

    delete data["keyPress"];
    data.phoneNumber = newPhoneNumber;

    if (!data.isMinor) {
      delete data["guardian"];
    }
    await clientApi
      .create(data)
      .then((res) => {
        toast.success("Register Success");
        this.props.history.push("/provider/clients");
      })
      .catch((err) => {
        const errors = err.response.data;
        this.focusFirstError(errors);
        this.setState({ errors });
        toast.error("Register Fail");
      });
  };

  updateClient = () => {
    let newPhoneNumber = this.state.phoneNumber.replaceAll("-", "");

    const data = this.state;
    delete data["keyPress"];
    data.phoneNumber = newPhoneNumber;

    delete data["firstSeenAt"];
    if (!data.isMinor && getYearOld(data.dateOfBirth) > 18) {
      delete data["guardian"];
    }
    if (_.isNull(data.dateOfBirth)) {
      delete data["dateOfBirth"];
    }
    if (!_.isNull(data.email)) {
      delete data["email"];
    }
    clientApi
      .update(data)
      .then((res) => {
        toast.success("Update Success");
        this.setState({ error: {} });
      })
      .catch((err) => {
        const errors = err.response.data;
        this.focusFirstError(errors);
        this.setState({ errors });
        toast.error("Update Fail");
      });
  };

  deleteClientAccount = () => {
    const id = this.state.id;
    clientApi
      .delete(id)
      .then(() => {
        toast.success("Delete Account success !");
        this.props.history.push("/provider/clients");
      })
      .catch((err) => {
        const errors = err.response.data;
        this.setState({ errors });
        toast.error("Delete Account fail !");
      });
  };

  toggleConfirmDelete = () => {
    this.setState({ isDeleteAccount: !this.state.isDeleteAccount });
  };

  getValue = (text) => {
    if (!text || text === "null" || text === "undefined") {
      return "";
    }
    return text || "";
  };

  render() {
    const client = this.state;
    const {
      errors,
      isMinor,
      clientData,
      documents,
      isEditAccount,
      isGuardianFail,
      guardian,
      status,
      firstName,
      us_state,
      startDate,
    } = this.state;
    const fullName = getUserFullName(client);
    const checkActive = status === 0 ? true : false;
    return (
      <Container className="client-edit-screen mt-6">
        <Card className="mt-3">
          <ConfirmDeleteModal
            className="modal-dialog-centered"
            isOpen={this.state.isDeleteAccount}
            toggle={this.toggleConfirmDelete}
            handle={this.deleteClientAccount}
            content={`Do you want to delete "${fullName}" account?`}
          />
          <CardHeader className="text-center border-0 pb-0">
            {isEditAccount && (
              <div className="font-weight-bold">Edit Client: {fullName}</div>
            )}
          </CardHeader>
          <CardBody>
            <Row>
              <Col md={12}>
                <FormGroup className="custom-control custom-checkbox">
                  <Input
                    className="custom-control-input"
                    id="activeClient"
                    type="checkbox"
                    checked={checkActive}
                    name="status"
                    onChange={this.handleCheckBox}
                  />
                  <Label
                    className="custom-control-label"
                    htmlFor="activeClient"
                  >
                    Active Client
                  </Label>
                </FormGroup>
              </Col>

              {/* start date component */}

              {/* <Col md={6}>
                <FormGroup>
                  <Label className="form-control-label">Date Started</Label>
                  <ReactDatetime
                    isValidDate={this.validDate}
                    timeFormat={false}
                    dateFormat="MM/DD/YYYY"
                    closeOnSelect={true}
                    inputProps={{
                      placeholder: `${"MM/DD/YYYY"}`,
                    }}
                    onChange={(date) => this.setStartDate(date)}
                  />
                </FormGroup>
              </Col> */}
              <Col md={12}>
                <h3>Name</h3>
              </Col>
              <Col md={6}>
                <FormGroup>
                  <Label className="form-control-label">
                    {" "}
                    First Name <sup className="text-required">*</sup>
                  </Label>
                  <FormInput
                    type="text"
                    name="firstName"
                    handleError={this.handleError}
                    errors={errors["firstName"]}
                    onChange={this.handleChange}
                    required={true}
                    value={this.getValue(firstName)}
                  />
                </FormGroup>
              </Col>
              <Col md={6}>
                <FormGroup>
                  <Label className="form-control-label"> Middle Name </Label>
                  <FormInput
                    type="text"
                    name="middleName"
                    handleError={this.handleError}
                    errors={errors["middleName"]}
                    onChange={this.handleChange}
                    value={this.getValue(client.middleName)}
                  />
                </FormGroup>
              </Col>
              <Col md={6}>
                <FormGroup>
                  <Label className="form-control-label">
                    {" "}
                    Last Name <sup className="text-required">*</sup>
                  </Label>
                  <FormInput
                    type="text"
                    name="lastName"
                    handleError={this.handleError}
                    errors={errors["lastName"]}
                    onChange={this.handleChange}
                    required={true}
                    value={this.getValue(client.lastName)}
                  />
                </FormGroup>
              </Col>
              <Col md={6}>
                <FormGroup>
                  <Label className="form-control-label"> Preferred name </Label>
                  <FormInput
                    type="text"
                    name="preferredName"
                    handleError={this.handleError}
                    errors={errors["preferredName"]}
                    onChange={this.handleChange}
                    value={this.getValue(client.preferredName)}
                  />
                </FormGroup>
              </Col>
              <Col md={6}>
                <FormGroup>
                  <Label className="form-control-label"> Suffix </Label>
                  <FormInput
                    type="text"
                    name="suffix"
                    handleError={this.handleError}
                    errors={errors["suffix"]}
                    onChange={this.handleChange}
                    defaultValue={this.getValue(client.suffix)}
                  />
                </FormGroup>
              </Col>
            </Row>
            <hr />
            <Row>
              <Col md={12}>
                <h3>Contact</h3>
              </Col>
              <Col md={6}>
                <FormGroup>
                  <Label className="form-control-label">
                    {" "}
                    Email <sup className="text-required">*</sup>
                  </Label>
                  <FormInput
                    type="text"
                    name="email"
                    handleError={this.handleError}
                    errors={errors["email"]}
                    onChange={this.handleChange}
                    required={true}
                    regex="email"
                    value={this.getValue(client.email)}
                    disabled={isEditAccount}
                  />
                </FormGroup>
              </Col>
              <Col md={6}>
                <FormGroup className="special-mobile-input">
                  <Label className="form-control-label">
                    {" "}
                    Phone Number <sup className="text-required">*</sup>{" "}
                  </Label>
                  <FormInput
                    type="text"
                    name="phoneNumber"
                    handleError={this.handleError}
                    errors={errors["phoneNumber"]}
                    onChange={this.handleChange}
                    regex="phoneNumber"
                    // placeholder="714-253-7445"
                    value={this.getValue(client.phoneNumber)}
                    autoFocus
                    required={true}
                  />
                </FormGroup>
              </Col>
              <Col md={6}>
                <FormGroup>
                  <Label className="form-control-label"> Address </Label>
                  <FormInput
                    type="text"
                    name="address"
                    handleError={this.handleError}
                    errors={errors["address"]}
                    onChange={this.handleChange}
                    value={this.getValue(client.address)}
                  />
                </FormGroup>
              </Col>
              <Col md={6}>
                <FormGroup>
                  <Label className="form-control-label"> City </Label>
                  <Input
                    type="text"
                    value={this.getValue(client.city)}
                    onChange={this.handleChange}
                    name="city"
                  ></Input>
                </FormGroup>
              </Col>
              <Col md={6}>
                <FormGroup>
                  <Label className="form-control-label"> State </Label>
                  <Input
                    type="select"
                    value={client.state}
                    onChange={(e) => this.handleChangeState(e, "state")}
                  >
                    <option value="">Select</option>
                    {us_state &&
                      us_state.map((item) => {
                        return (
                          <option key={item.code} value={item.code}>
                            {item.name}
                          </option>
                        );
                      })}
                  </Input>
                </FormGroup>
              </Col>
              <Col md={6}>
                <FormGroup>
                  <Label className="form-control-label"> Zip Code </Label>
                  <FormInput
                    type="text"
                    name="zipcode"
                    handleError={this.handleError}
                    errors={errors["zipcode"]}
                    onChange={this.handleChange}
                    value={this.getValue(client.zipcode)}
                  />
                </FormGroup>
              </Col>
            </Row>
            <hr />
            <Row>
              <Col md={12}>
                <h3>Personal Information</h3>
              </Col>
              <Col md={6}>
                <FormGroup>
                  <Label className="form-control-label">Date of Birth</Label>
                  <ReactDatetime
                    isValidDate={this.validDate}
                    timeFormat={false}
                    dateFormat="MM/DD/YYYY"
                    closeOnSelect={true}
                    inputProps={{
                      placeholder: `${
                        client.dateOfBirth
                          ? getDateFormat(client.dateOfBirth)
                          : "MM/DD/YYYY"
                      }`,
                    }}
                    onChange={(date) => this.setDates(date)}
                  />
                  <div className={` ${client.dateOfBirth ? "mt-3" : "d-none"}`}>
                    <small>
                      {getYearOld(client.dateOfBirth)} <i>years old</i>
                    </small>
                  </div>
                  <FormGroup
                    className={isMinor || guardian ? "mt-2" : "d-none"}
                  >
                    <Label className="form-control-label">
                      Guardian <sup className="text-required">*</sup>
                    </Label>
                    <Input
                      type="select"
                      onChange={this.handleChange}
                      name="guardian"
                      value={client.guardian || ""}
                    >
                      <option disabled value="">
                        ---select---
                      </option>
                      {clientData &&
                        clientData.map((item, i) => {
                          const fullName = getUserFullName(item);
                          return (
                            <option
                              key={i}
                              value={item.id}
                              className={item.id === client.id ? "d-none" : ""}
                            >
                              {fullName}
                            </option>
                          );
                        })}
                    </Input>
                  </FormGroup>
                </FormGroup>
              </Col>
              <Col md={6}>
                <FormGroup>
                  <Label className="form-control-label">Gender</Label>
                  <Input
                    type="select"
                    value={client.gender}
                    onChange={(e) => this.handleSelectionChange(e, "gender")}
                  >
                    {cons.GENDER.map((item, i) => {
                      return (
                        <option key={i} value={item.id}>
                          {item.gender}
                        </option>
                      );
                    })}
                  </Input>
                </FormGroup>
              </Col>
              <Col md={6}>
                <FormGroup>
                  <Label className="form-control-label">
                    {" "}
                    Relationship Status{" "}
                  </Label>
                  <Input
                    type="select"
                    value={client.relationshipStatus}
                    onChange={(e) =>
                      this.handleSelectionChange(e, "relationshipStatus")
                    }
                  >
                    {cons.RELATIONSHIP_INFO.map((item, i) => {
                      return (
                        <option key={i} value={item.id}>
                          {item.relationshipStatus}
                        </option>
                      );
                    })}
                  </Input>
                </FormGroup>
              </Col>
              <Col md={6}>
                <FormGroup>
                  <Label className="form-control-label">
                    {" "}
                    Preferred Language{" "}
                  </Label>
                  <Input
                    type="select"
                    value={client.preferredLanguage}
                    onChange={(e) =>
                      this.handleSelectionChange(e, "preferredLanguage")
                    }
                  >
                    {cons.LANGUAGE.map((item, i) => {
                      return (
                        <option key={i} value={item.code}>
                          {item.preferredLanguage}
                        </option>
                      );
                    })}
                  </Input>
                </FormGroup>
              </Col>
              <Col md={6}>
                <FormGroup>
                  <Label className="form-control-label"> Ethnicity </Label>
                  <Input
                    type="select"
                    value={client.race}
                    onChange={(e) => this.handleSelectionChange(e, "race")}
                  >
                    {cons.RACE.map((item, i) => {
                      return (
                        <option key={i} value={item.id}>
                          {item.race}
                        </option>
                      );
                    })}
                  </Input>
                </FormGroup>
              </Col>
              <Col md={6}>
                <FormGroup>
                  <Label className="form-control-label">
                    Employment Status
                  </Label>
                  <Input
                    type="select"
                    value={client.employmentStatus}
                    onChange={(e) =>
                      this.handleSelectionChange(e, "employmentStatus")
                    }
                  >
                    {cons.EMPLOYMENT.map((item, i) => {
                      return (
                        <option key={i} value={item.id}>
                          {item.employmentStatus}
                        </option>
                      );
                    })}
                  </Input>
                </FormGroup>
              </Col>

              {/* <Col md={12} className="m-2">
                <FormGroup>
                  <Label className="form-control-label">
                    Additional Comments
                  </Label>
                  <FormInput
                    type="text"
                    name="comments"
                    handleError={this.handleError}
                    errors={errors["comments"]}
                    // onChange={this.handleChange}
                    // value={this.getValue(client.address)}
                  />
                </FormGroup>
              </Col> */}
              {/* <Col md={12} style={{ display: "flex", marginBottom: "30px" }}>
                <div>
                  <Input
                    accept="file/*"
                    id="text-button-file"
                    className="d-none"
                    type="file"
                    multiple
                  />
                  <div className="mr-3 mt-3">
                    <label className="mr-2 text-muted"></label>
                    <label
                      style={{ cursor: "pointer" }}
                      htmlFor="text-button-file"
                    >
                      <i className="fas fa-paperclip fa-lg"></i>
                    </label>
                  </div>
                </div>
                <Button className="text-right" color="info">
                  Attach Files
                </Button>
              </Col> */}

              <Col md={8} className="m-2">
                <Label className="form-control-label"> Signed Documents </Label>
                {documents.map((item, i) => {
                  return (
                    <FormGroup className="custom-checkbox ml-4" key={i}>
                      <Input
                        className="custom-control-input"
                        id={item.name}
                        type="checkbox"
                        name={i}
                        checked={item.isUsable}
                        onChange={this.handleMultipleSelectDocument}
                      />
                      <Label
                        className="custom-control-label"
                        htmlFor={item.name}
                      >
                        {item.name}
                      </Label>
                    </FormGroup>
                  );
                })}
              </Col>

              <Col md={12} className={!isEditAccount ? "d-none" : "text-right"}>
                <Label className="join-date">
                  Date First Seen :{" "}
                  {getDateTime(client.firstSeenAt) || (
                    <span className="text-danger">N/A</span>
                  )}
                </Label>
              </Col>
            </Row>
            <ErrorSummary error={errors} className="mt-3" />
          </CardBody>
          <CardFooter>
            <Row>
              <Col md={12}>
                <li
                  className={`text-danger mb-4 ${
                    isGuardianFail ? "" : "d-none"
                  }`}
                >
                  {" "}
                  Please select Guardian
                </li>
              </Col>
              <Col md={6} className="mb-3 md-md-0 text-center text-md-left">
                <Button
                  color="danger"
                  onClick={this.toggleConfirmDelete}
                  className={!isEditAccount ? "d-none" : ""}
                >
                  {" "}
                  Delete Account{" "}
                </Button>
              </Col>
              <Col md={6} className="mb-3 md-md-0">
                <Row className="justify-content-md-end justify-content-center">
                  <Link
                    to={`/provider/clients/${client.id}`}
                    className="d-block"
                  >
                    <Button color="danger" outline>
                      Cancel
                    </Button>
                  </Link>
                  <Button
                    className="ml-2"
                    color="success"
                    onClick={this.saveForm}
                    disabled={isGuardianFail}
                  >
                    Save
                  </Button>
                </Row>
              </Col>
            </Row>
          </CardFooter>
        </Card>
      </Container>
    );
  }
}
